import React from "react"
import {BrowserRouter as Router, Route, Switch, Redirect, NavLink} from "react-router-dom"

import Recommend from "./recommend/Recommend"
import Ranking from "./ranking/Ranking"
import Search from "../containers/Search"
import MusicPlayer from "./play/MusicPlayer"

import Singer from "../containers/Singer"

import logo from "../assets/imgs/logo.png"
import "../assets/stylus/font.styl"
import "./App.styl"

class App extends React.Component {
  render() {
    return (
        <Router>
          <div className="app">
            <header className="app-header">
              <img src={logo} className="app-logo" alt="logo" />
              <h1 className="app-title">Mango Music</h1>
            </header>
            <div className="music-tab">
              <div className="tab-item">
                <NavLink to="/mango-music/recommend" className="nav-link">
                  <span>推荐</span>
                </NavLink>
              </div>
              <div className="tab-item">
                <NavLink to="/mango-music/ranking" className="nav-link">
                  <span>排行榜</span>
                </NavLink>
              </div>
              <div className="tab-item">
                <NavLink to="/mango-music/search" className="nav-link">
                  <span>搜索</span>
                </NavLink>
              </div>
            </div>
            <div className="music-view">
              {/*
                Switch组件用来选择最近的一个路由，否则最后一个没有指定path的路由也会显示
                Redirect重定向到列表页
              */}
              <Switch>
                <Route path="/mango-music/recommend" component={Recommend} />
                <Route path="/mango-music/ranking" component={Ranking} />
                <Route path="/mango-music/search" component={Search} />
                <Route path="/mango-music/singer/:id" component={Singer}/>
                <Redirect from="/" to="/mango-music/recommend" />
                <Route component={Recommend} />
              </Switch>
            </div>
            <MusicPlayer/>
          </div>
        </Router>
    );
  }
}

export default App;



